.footer {
  // outline: solid 2px salmon;
  padding-bottom: 15px;

  @media screen and (min-width: 768px) {
    padding-bottom: 28px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 40px;
  }
}

.nav-list {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.nav-li-deep:hover,
.nav-li-deep:focus {
  color: #9bb537;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.nav-li-deep {
  color: #050505;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  @media screen and (min-width: 768px) {
    font-weight: 500;
  }
  @media screen and (min-width: 1280px) {
    font-weight: 400;
  }
}

.logo-btn {
  color: #9bb537;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.125; /* 112.5% */
  letter-spacing: -0.64px;
  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 1.11; /* 111.111% */
    letter-spacing: -0.72px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.11; /* 111.111% */
    letter-spacing: -0.72px;
  }
}

