.contacts-list {
  display: flex;
  // outline: solid 1px salmon;
}

.contacts-block {
  text-align: center;
  margin-top: 15px;
}
.contacts {
  color: #050505;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.33; /* 116.667% */
  text-transform: uppercase;
  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.72px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.72px;
  }
}
.contacts-list {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  gap: 15px;
}
.modal-icon {
  border-radius: 7px;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.contact-li {
  a {
    display: flex;
    width: auto;
    height: auto;
    &:hover .modal-icon,
    &:focus .modal-icon {
      fill: $accentColor;
    }
  }
  // :hover,
  // .modal-icon:focus {
  //   background-color: #9bb537;
  //   border-radius: 7px;
  // }
}
