.sub-block {
  margin-top: 15px;
  // margin: 0 auto;
}
.sub-text {
  text-align: center;
  color: #050505;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.33; /* 116.667% */
  text-transform: uppercase;
  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.72px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.72px;
  }
}
.sub-btn {
  color: #fff;
  border-style: none;
  border-radius: 15px;
  background-color: #9bb537;
  border: 1px solid $accentColor;
  padding: 8px 24px 8px 24px;
  width: 165px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: 0.04em;

  transition: all $cubic-bezier;
}
.sub-btn:hover,
.sub-btn:focus {
  color: #050505;
  background-color: transparent;
}

.sub-form {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: 10px;
  margin-top: 15px;
  input {
    color: rgba(5, 5, 5, 0.5);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.25; /* 125% */

    padding-left: 16px;
    height: 40px;
    width: 264px;
    margin-right: 24px;
    background-color: var(--primary-text-color);
    border-radius: 15px;
    border: none;
    outline: 1px solid rgba(5, 5, 5, 0.2);
    transition: all $cubic-bezier;

    &:hover,
    &:focus {
      outline: 1px solid $accentColor;
    }
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
    color: rgb(0, 0, 0);
    // border-radius: 4px;
  }
}

.final-text {
  text-align: center;
  margin-top: 15px;

  color: #050505;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  @media screen and (min-width: 768px) {
    font-weight: 500;
  }
  @media screen and (min-width: 1280px) {
    font-weight: 400;
  }
}

.is-active {
  color: #9bb537;
}

.scroll-to-the-top-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  padding: 0;
  line-height: 0;
  background-color: transparent;
  border: none;

  opacity: 0;
  pointer-events: none;
  visibility: hidden;

  transition: all $cubic-bezier;
}
.scroll-to-the-top-svg {
  transition: fill $cubic-bezier;
  fill: #050505;
}

.scroll-to-the-top-btn:hover .scroll-to-the-top-svg,
.scroll-to-the-top-btn:focus .scroll-to-the-top-svg {
  fill: $accentColor;
}
